import React, { useState } from 'react'
import { NextSeo } from 'next-seo'
import Head from 'next/head'

import {
  LanguageProvider, defaultLanguage
} from '../components/language'
import {
  GlobalProvider
} from '../components/global'
import GA from '../components/GA'
import Desktop from '../components/Desktop'
import AIR from '../components/AIR'
import Album from '../components/Album'
import SetLanguage from '../components/SetLanguage'
import TPP from '../components/TPP'
import HID from '../components/HID'
import Music from '../components/Music'
import WIS from '../components/WIS'
import GAM from '../components/GAM'
import Feedback from '../components/Feedback'
import Readme from '../components/Readme'

const Index = props => {
  const [{ language }, setLanguage] = useState({ language: defaultLanguage })
  const changeLanguage = language => setLanguage({ language })
  const { appId, appParams, gaId } = props

  return (
    <LanguageProvider value={{
      language,
      changeLanguage: changeLanguage
    }}>
      <NextSeo
        title={language('My Animal Crossing Life')}
        description='A group of WebApps for Animal Crossing.'
      />
      <Head>
        <link rel='shortcut icon' href='/favicon.png' />
        <meta name='viewport' content='width=device-width, initial-scale=1' />
      </Head>
      <GlobalProvider value={{ appId, appParams, gaId }}>
        <GA gid={gaId} aid={'index'} />
        <Desktop>
          <HID />
          <TPP />
          <GAM />
          <WIS />
          <SetLanguage />
          <div />
          <div />
          <div />
        </Desktop>
      </GlobalProvider>
    </LanguageProvider>
  )
}

Index.getInitialProps = ({ query }) => query

export default Index
